.navigation {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 37px;

  &__main {
    display: flex;
    margin-bottom: 17px;

    a {
      text-decoration: none;
      padding: 10px;
      color: $buttonTextColor;
      font-size: 13px;

      &:hover {
        color: #000000;
        transition: 0.4s ease-in;
      }

      &:last-child {
        border: 0.75px solid $altSectionBackgroundColor;
      }
    }

    &__auth {
      display: flex;
      align-items: center;

      p {
        font-size: 14px;
        color: #000000;

      }

      a {
        color: #000000;
        border: 0.75px solid $altSectionBackgroundColor;
        padding: 9px 7px;
        margin: 0 20px;

        &:hover {
          background-color: $sectionBackgroundColor;
          transition: 0.4s ease-in;
        }
      }

      button {
        font-size: 12px;
        color: $buttonTextColor;
        background: none;
        border: none;
        cursor: pointer;

        &:hover {
          color: #000000;
          transition: 0.4s ease-in;
        }
      }
    }

  }

  &__homepage {
    ul {
      display: flex;
      list-style: none;

      a {
        text-decoration: none;
        cursor: pointer;
        color: $primaryTextColor;
      }

      li {
        padding: 10px 20px;
        font-size: 18px;
        border: 1px solid transparent;

        &:hover {
          border: 1px solid $primaryTextColor;
          transition: 0.3s ease-in;
        }
      }
    }
  }
}