.whoWeHelp {
  display: flex;
  flex-direction: column;
  align-items: center;

  h1 {
    margin-top: 70px;
    margin-bottom: 25px;
    font-size: 38px;
    line-height: 55px;
    font-weight: 400;
  }

  img {
    margin-bottom: 50px;
  }

  &__options {
    display: flex;
    width: 750px;
    justify-content: space-evenly;

    button {
      outline: none;
      background: white;
      border: none;
      font-family: inherit;
      cursor: pointer;
      color: inherit;
      max-width: 210px;
      font-size: 24px;
      font-weight: 400;

      &:hover {
        background-color: $sectionBackgroundColor;
        transition: 0.4s ease-in;
      }

      &:first-child {
        padding: 23px 42px;
      }

      &:nth-child(2) {
        padding: 12px 19px;
      }

      &:last-child {
        padding: 12px 50px;
      }

    }
  }

  .option__details {
    max-width: 1300px;
    width: 100%;

    .description {
      width: 640px;
      text-align: center;
      margin: 70px auto;
      font-size: 22px;
      line-height: 30px;
      font-weight: 400;
    }
  }

  .option__list {
    list-style: none;
    font-family: $font-alt, "sans-serif";
    font-weight: 300;

    li {
      width: 100%;
    }

    &-text {
      display: flex;
      justify-content: space-between;
      align-items: center;

      div {
        p:first-child {
          font-size: 26px;
          line-height: 31px;
          margin-bottom: 6px;
        }

        p:nth-child(2) {
          font-size: 18px;
          font-style: italic;
        }
      }

      span {
        font-size: 18px;
      }
    }

    hr {
      margin-top: 31px;
      margin-bottom: 34px;
    }

    &-pageNumbers {
      list-style: none;
      display: flex;
      justify-content: center;

      li {
        padding: 13px 15px;
        cursor: pointer;
        margin-right: 10px;

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

}